<template>
  <div class="palyview">
    <audio :src="curentSongUrl" autoplay loop></audio>
    <template v-if="curentBar">
      <div class="play-bar">
        <div @click="isShowFull=true">
          <img :src="curentBar.al.picUrl" :class="{'paused':isPaused}" />
          <span class="bb fa fa-expand"></span>
        </div>
        <h3>{{curentBar.name}}</h3>
        <span
          class="progress fa fa-play"
          @click.stop="pauseAudio"
          :class="{ 'fa-pause': !isPaused }"
        >
          <canvas width="40px" height="40px"></canvas>
        </span>
      </div>
      <template v-if="isShowFull">
        <div class="play-full">
          <div class="fubg" :style="{'background-image':`url(${curentBar.al.picUrl}`}"></div>
          <span class="proo fa fa-compress" @click="isShowFull=false,ifgeci=false"></span>
          <div class="fu-bar" v-if="!ifgeci">
            <img class="toppic" :class="{'issticon':isPaused}" src="../assets/needle-ip6.png" alt />
            <img
              class="t"
              :src="curentBar.al.picUrl"
              @click="ifgeci=!ifgeci"
              :class="{'paused':isPaused}"
            />
            <h3>{{curentBar.name}} - {{curentBar.ar[0].name}}</h3>
          </div>
          <template v-if="ifgeci">
            <div class="geci" :class="{'scrolling':iftouch}" @click.stop="ifgeci=!ifgeci">
              <h2>{{curentBar.name}}</h2>
              <h3 class="geli">{{curentBar.ar[0].name}}</h3>
              <!-- ========================= -->
              <ul
                @scroll="scroll"
                @touchstart="touchStart"
                @touchmove="touchMove"
                @touchend="touchEnd"
              >
                <li v-for="(item, index) in geci" :key="index">{{item.text}}</li>
              </ul>
            </div>
          </template>
          <div class="kongzhiqi">
            <span class="loopMod">
              <svg
                t="1592563988263"
                class="icon"
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                p-id="1535"
                width="32"
                height="32"
              >
                <path
                  d="M928 476.8c-19.2 0-32 12.8-32 32v86.4c0 108.8-86.4 198.4-198.4 198.4H201.6l41.6-38.4c6.4-6.4 12.8-16 12.8-25.6 0-19.2-16-35.2-35.2-35.2-9.6 0-22.4 3.2-28.8 9.6l-108.8 99.2c-16 12.8-12.8 35.2 0 48l108.8 96c6.4 6.4 19.2 12.8 28.8 12.8 19.2 0 35.2-12.8 38.4-32 0-12.8-6.4-22.4-16-28.8l-48-44.8h499.2c147.2 0 265.6-118.4 265.6-259.2v-86.4c0-19.2-12.8-32-32-32zM96 556.8c19.2 0 32-12.8 32-32v-89.6c0-112 89.6-201.6 198.4-204.8h496l-41.6 38.4c-6.4 6.4-12.8 16-12.8 25.6 0 19.2 16 35.2 35.2 35.2 9.6 0 22.4-3.2 28.8-9.6l105.6-99.2c16-12.8 12.8-35.2 0-48l-108.8-96c-6.4-6.4-19.2-12.8-28.8-12.8-19.2 0-35.2 12.8-38.4 32 0 12.8 6.4 22.4 16 28.8l48 44.8H329.6C182.4 169.6 64 288 64 438.4v86.4c0 19.2 12.8 32 32 32z"
                  p-id="1536"
                  fill="#ffffff"
                />
                <path
                  d="M544 672V352h-48L416 409.6l16 41.6 60.8-41.6V672z"
                  p-id="1537"
                  fill="#ffffff"
                />
              </svg>
            </span>
            <span
              class="fa fa-step-backward"
              @click="prvePlay"
              :class="{'hasSong':overList==false}"
            ></span>

            <span class="fa fa-play" v-if="isPaused" @click="pauseAudio"></span>
            <span class="fa fa-stop" v-else @click="pauseAudio"></span>

            <span class="fa fa-step-forward" @click="nextPlay"></span>
            <span class="sound" @click="showsd" @mouseenter="soundCL">
              <svg
                v-if="sound!=0"
                t="1592554092329"
                class="icon"
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                p-id="2057"
                width="32"
                height="32"
              >
                <path
                  d="M64 334.9v392l227.7-12.6 366.7 215V94.7L279 334.9zM738.5 324.4c-12.6-8.4-29.9-5.1-38.3 7.6-8.4 12.6-5.1 29.9 7.6 38.3 0.5 0.4 178.9 116.2 1.3 290.6-11 10.7-11.1 28.2-0.4 39.1 10.7 11 28.2 11.1 39.1 0.4 226.6-222.7-8.6-375.5-9.3-376z"
                  p-id="2058"
                  fill="#ffffff"
                />
                <path
                  d="M960 497.4l-0.1-1.9c-17.4-179.3-158.6-258.7-159-258.9-13.2-7.6-30.2-3.1-37.8 10.1-7.6 13.2-3.1 30.2 10.1 37.8 0.3 0.1 116.6 65.6 131.7 215.4 0.2 15.4 0.7 192.5-127.5 251.4-13.9 6.3-20 22.7-13.7 36.6s22.7 20 36.6 13.7C967.7 724.7 960 498 960 497.4z"
                  p-id="2059"
                  fill="#ffffff"
                />
              </svg>
              <svg
                v-else
                t="1592558295953"
                class="icon"
                viewBox="0 0 1412 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                p-id="2984"
                width="32"
                height="32"
              >
                <path
                  d="M1139.903986 627.775992C1147.519986 593.087993 1151.999986 554.559993 1151.999986 511.999994c0-278.399997-191.999998-383.999995-191.999998-383.999995l0 78.335999c0 0 127.999998 83.263999 127.999998 305.663996 0 28.48-2.112 54.591999-5.76 78.527999L1139.903986 627.775992z"
                  p-id="2985"
                  fill="#ffffff"
                />
                <path
                  d="M959.999988 511.487994 959.999988 511.487994C959.743988 271.487997 767.999991 191.999998 767.999991 191.999998l0 71.103999c0 0 108.671999 46.079999 125.759998 205.567998L959.999988 511.487994z"
                  p-id="2986"
                  fill="#ffffff"
                />
                <path
                  d="M1311.871984 738.943991C1331.071984 679.103992 1343.999984 604.415993 1343.999984 511.999994c0-355.903996-191.999998-447.999995-191.999998-447.999995l0 83.071999c0 0 127.999998 70.655999 127.999998 364.927996 0 79.103999-9.28 142.015998-22.784 191.615998L1311.871984 738.943991z"
                  p-id="2987"
                  fill="#ffffff"
                />
                <path
                  d="M125.567998 115.327999 1376.895983 939.263989 1411.839983 885.631989 160.511998 61.695999Z"
                  p-id="2988"
                  fill="#ffffff"
                />
                <path
                  d="M428.479995 167.935998 703.999991 346.047996 703.999991 0Z"
                  p-id="2989"
                  fill="#ffffff"
                />
                <path
                  d="M1151.999986 959.999988c0 0 29.76-14.272 65.791999-53.247999l-57.215999-36.992C1155.199986 873.791989 1151.999986 875.519989 1151.999986 875.519989L1151.999986 959.999988z"
                  p-id="2990"
                  fill="#ffffff"
                />
                <path
                  d="M0 375.487995l0 273.087997c0 0 0 68.287999 66.687999 68.287999l133.311998 0L703.999991 1023.999988 703.999991 574.655993 246.399997 278.911997 199.999998 307.199996 66.687999 307.199996C0 307.199996 0 375.487995 0 375.487995z"
                  p-id="2991"
                  fill="#ffffff"
                />
                <path
                  d="M767.999991 760.895991 767.999991 831.99999c0 0 82.687999-34.304 139.519998-125.759998l-50.495999-32.64C819.39199 739.071991 767.999991 760.895991 767.999991 760.895991z"
                  p-id="2992"
                  fill="#ffffff"
                />
                <path
                  d="M959.999988 814.97599 959.999988 895.999989c0 0 50.175999-27.584 99.199999-91.775999l-52.479999-33.92C981.439988 801.47199 959.999988 814.97599 959.999988 814.97599z"
                  p-id="2993"
                  fill="#ffffff"
                />
              </svg>
              <input
                type="range"
                name
                id
                class="sdbar"
                @touchend="barEnd"
                v-model="sound"
                :class="{'issd':issdshow}"
              />
            </span>
          </div>
        </div>
      </template>
    </template>
    <!-- <img v-if="loading" src="../assets/timg.gif" alt /> -->
  </div>
</template>

<script>
export default {
  props: ["curentsongId", "showFull"],
  data() {
    return {
      curentBar: null,
      isPaused: false,
      currentTime: 0,
      geci: null,
      isShowFull: this.showFull || false,
      ifgeci: false,
      timer: true,
      currentIndex: 0,
      loading: false,
      iftouch: false,
      sound: 10,
      issdshow: false,
      simiList: [],
      simiListIndex: 0,
      overList: [],
      simiOverListIndex: 0
    };
  },
  computed: {
    curentSongUrl() {
      return (
        "https://music.163.com/song/media/outer/url?id=" +
        this.curentsongId +
        ".mp3"
      );
    },
    SimiOverListIndex() {
      return this.simiOverListIndex + this.overList.length - 1;
    }
  },
  watch: {
    curentsongId: function() {
      this.getCurentBar();
      this.isShowFull = this.showFull;
      this.getGeCi();
    },
    isPaused() {
      if (this.isPaused) {
        window.clearInterval(window.animateId);
      } else {
        window.clearInterval(this.animateId);
        window.animateId = this.drawCircle();
      }
    },
    curentBar() {
      this.$nextTick(function() {
        window.clearInterval(this.animateId);
        window.animateId = this.drawCircle();
      });
    },
    currentTime() {
      if (this.ifgeci && this.timer) {
        this.geciscroll();
      }
    },
    sound() {
      this.soundCL();
    }
  },
  methods: {
    getCurentBar() {
      this.loading = true;

      this.axios
        .get("http://music.kele8.cn/song/detail", {
          params: {
            ids: this.curentsongId
          }
        })
        .then(response => {
          this.curentBar = response.data.songs[0];
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getGeCi() {
      this.axios
        .get("http://music.kele8.cn/lyric", {
          params: {
            id: this.curentsongId
          }
        })
        .then(response => {
          this.geci = response.data.lrc.lyric;
          let arr = [];
          this.geci.split("\n").forEach((element, index) => {
            var t = element.slice(1, 10);
            arr[index] = {
              time: Number(t.slice(0, 2)) * 60 + Number(t.slice(3)),
              text: element.slice(11).trim()
            };
          });
          arr.pop();
          this.geci = arr;
        });
    },
    pauseAudio() {
      let audio = this.$el.querySelector("audio");
      if (this.isPaused) {
        audio.play();
      } else {
        audio.pause();
      }
      this.isPaused = audio.paused;
    },
    drawCircle() {
      let audio = this.$el.querySelector("audio");
      let canvas = this.$el.querySelector("canvas");
      let ctx = canvas.getContext("2d");

      let id = window.setInterval(() => {
        this.currentTime = audio.currentTime * 1000;

        ctx.clearRect(0, 0, 40, 40);

        ctx.beginPath();
        ctx.arc(20, 20, 19, 0, 2 * Math.PI);
        ctx.closePath();
        ctx.strokeStyle = "#ccc";
        ctx.stroke();

        ctx.beginPath();
        ctx.arc(
          20,
          20,
          19,
          -0.5 * Math.PI,
          2 * Math.PI * (this.currentTime / this.curentBar.dt) - 0.5 * Math.PI
        );
        ctx.strokeStyle = "red";
        ctx.stroke();
      }, 500);
      return id;
    },
    geciscroll() {
      // let audio = this.$el.querySelector("audio");
      let geciBox = document.querySelector(".geci ul");
      let geci = this.geci;

      let lis = document.querySelectorAll(".geci ul li");
      let currentTime = this.currentTime / 1000;

      // window.console.log(currentTime);
      for (let i = 0; i < geci.length; i++) {
        if (geci[i].time >= currentTime) {
          this.currentIndex = i;
          break;
        }
      }
      lis.forEach((element, index) => {
        if (index == this.currentIndex) {
          if (index < 1) {
            lis[0].classList.add("active");
          } else if (index == geci.length - 1) {
            lis[index].classList.add("active");
          } else {
            lis[index - 1].classList.add("active");
          }
        } else {
          lis[index].classList.remove("active");
        }
      });

      let listop = lis[this.currentIndex].offsetTop;
      let boxtop = geciBox.offsetTop;

      if (listop - boxtop > 220) {
        geciBox.scrollTo({
          top: listop - boxtop - 220,
          behavior: "smooth"
        });
      }

      //
    },
    // 音量
    soundCL() {
      let audio = this.$el.querySelector("audio");
      audio.volume = this.sound / 100;
    },
    showsd() {
      if (this.issdshow) {
        this.sound = 0;
      } else {
        this.issdshow = true;
      }
    },
    barEnd() {
      this.issdshow = false;
    },
    //歌词拖动
    scroll(event) {
      event.preventDefault();
    },
    touchStart() {
      this.timer = false;
    },
    touchMove() {
      let geciBox = document.querySelector(".geci ul");

      this.currentIndex = Math.round(geciBox.scrollTop / 20 + 1);
      this.iftouch = true;
    },
    touchEnd() {
      let audio = this.$el.querySelector("audio");
      this.timer = true;
      audio.currentTime = this.geci[this.currentIndex - 1].time;
      this.geci;
      this.iftouch = false;
    },
    // 歌曲切换
    startPlayList() {
      if (this.curentsongId) {
        this.axios
          .get("http://music.kele8.cn/simi/song", {
            params: {
              id: this.curentsongId
            }
          })
          .then(res => {
            // console.log(res.data.songs);
            res.data.songs.forEach(e => {
              // console.log(e.id);
              this.simiList.push(e.id);
            });
          })
          .catch(err => {
            window.console.error(err);
          });
      }
    },
    nextPlay() {
      if (this.simiList) {
        this.simiList.forEach((e, i, arr) => {
          if (this.simiListIndex == i) {
            this.overList.push(this.curentsongId);

            this.$emit("nextPlay", { songID: e });
            // window.console.log(this.simiListIndex == arr.length);
            if (this.simiListIndex == arr.length - 1) {
              this.simiList = [];
              this.simiListIndex = -1;
              this.startPlayList();
            }
          }
        });
        this.simiListIndex++;
      }
    },
    prvePlay() {
      if (this.overList) {
        this.overList.forEach((e, i) => {
          if (this.SimiOverListIndex == i) {
            // console.log(e, this.SimiOverListIndex);
            this.$emit("prvePlay", { songID: e });
          }
        });
        this.simiListIndex--;
      } else {
        return;
      }
    }
  },
  // 生命周期
  created() {
    this.getCurentBar();
    this.getGeCi();
  },
  mounted() {
    this.soundCL();
    this.startPlayList();
  },
  updated() {}
};
</script>

<style lang='less'>
audio {
  position: fixed;
  bottom: 30vh;
  left: 0;
  width: 100%;
  height: 70px;
}
.palyview {
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: aliceblue;
  width: 100vw;
  z-index: 10;
  .play-bar {
    display: flex;
    border: 1px solid #cccccc;
    padding: 10px;
    & > div {
      position: relative;
      img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        animation: myroll 10s linear infinite;
        &.paused {
          animation-play-state: paused;
        }
      }
      .bb {
        color: white;
        font-size: 25px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
      }
    }

    @keyframes myroll {
      from {
        transform: rotate(0);
      }
      to {
        transform: rotate(360deg);
      }
    }
    h3 {
      flex: 1;
      margin: 0 10px;
      line-height: 40px;
    }
    .progress {
      width: 40px;
      height: 40px;
      text-align: center;
      line-height: 40px;
      color: #999999;
      position: relative;
      canvas {
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    .proo {
      width: 40px;
      height: 40px;
      text-align: center;
      line-height: 40px;
      font-size: 28px;
      color: #999999;
    }
  }
  .play-full {
    width: 100vw;
    height: 100vh;
    color: white;
    position: relative;
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.3);
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
    }
    span {
      color: #ffffff;
      // line-height: 30px;
      padding: 10px;
      font-size: 30px;
      position: relative;
      z-index: 99;
    }
    .fubg {
      position: absolute;
      bottom: 0;
      left: 0;
      background-position: center center;
      background-size: cover;
      filter: blur(10px);
      width: 100%;
      height: 100%;
      z-index: -1;
    }
    .fu-bar {
      margin-top: 30px;
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 12px;

      img.toppic {
        transform-origin: top left;
        width: 94px;
        position: absolute;
        top: 0;
        left: 50%;
        z-index: 9;
        transform: translateX(-15%);
        transition: all 0.3s;

        //
      }
      img.issticon {
        transform-origin: top left;
        transform: rotate(-30deg);
      }

      img.t {
        width: 65vw;
        height: 65vw;
        border-radius: 50%;
        border: 10vw solid #333;
        animation: myroll 30s linear infinite;
        box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.3);
        &.paused {
          animation-play-state: paused;
        }
      }

      @keyframes myroll {
        from {
          transform: rotate(0);
        }
        to {
          transform: rotate(360deg);
        }
      }
      h3 {
        font-size: 12px;
        margin: 20px 10px;
      }
    }
    .kongzhiqi {
      width: 100%;
      padding-bottom: 10px;
      position: absolute;
      bottom: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: space-around;
      // background-color: rgba(0, 0, 0, );
      .hasSong {
        color: #9f9f9f;
      }
      span {
        font-size: 40px;
        position: relative;
      }
      .sound {
        .sdbar {
          transform: rotate(-90deg);
          position: absolute;
          left: -75%;
          top: -80%;
          display: none;
        }
        .issd {
          display: block;
        }
      }
    }
  }
}
.geci {
  width: 100%;
  // overflow-y: scroll;
  position: relative;
  top: 0;
  left: 0;
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 80vh;

  h3,
  h2 {
    font-size: 18px;
    line-height: 30px;
    &.geli {
      font-size: 16px;

      padding-bottom: 20px;
    }
  }

  &.scrolling {
    &::before {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      top: 40%;
      /* margin-top: 10px; */
      left: 0;
      border-top: 1px dashed rgb(195, 191, 191);
    }
  }

  ul {
    overflow-y: scroll;
    padding: 20vh 0;
  }

  li {
    padding: 0 50px;
    text-align: center;
    line-height: 25px;
    font-size: 14px;

    &.active {
      color: orange;
    }
  }
}
</style>