<template>
  <div class="hotop">
    <!-- coverImgUrl -->
    <div class="hoticon"></div>
    <div class="hottime">更新日期：{{nTime}}</div>
  </div>
</template>

<script>
export default {
  props: ["songUpdatetime"],
  data() {
    return {
      time: this.songUpdatetime.updateTime
    };
  },
  computed: {
    nTime: function() {
      var now=new Date(this.time)
      var month = now.getMonth() + 1; //取得日期中的月份，其中0表示1月，11表示12月
      var date = now.getDate(); //返回日期月份中的天数（1到31）
      return (0+
        month +
        "月" +
        date+"日"
      );
    }
  },
};
</script>

<style lang='less'>
.hotop {
  overflow: hidden;
  background: url("../assets/hot_music_bg.jpg") no-repeat;
  background-size: cover;
  padding: 20px;
  .hoticon {
    width: 142px;
    height: 67px;
    background: url("../assets/icon.png") no-repeat;
    background-repeat: no-repeat;
    background-position: -24px -30px;
    background-size: 166px 97px;
  }
  .hottime {
    margin-top: 10px;
    color: hsla(0, 0%, 100%, 0.8);
    font-size: 12px;
    transform: scale(0.91);
    transform-origin: left top;
  }
}
</style>